@import url(https://fonts.googleapis.com/css?family=Work+Sans);
@import url(https://fonts.googleapis.com/css2?family=Amatic+SC:wght@400;700&family=Piedra&family=Special+Elite&display=swap);
body {
  margin: 0;
  font-family: 'Work Sans';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #eceff1;
  color: #4d4d4d;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.Pokecard {
  /* border: 2px solid mediumseagreen; */
  /* width: 300px; */
  width: 200px;
  padding: 10px;
  margin: 1rem;
  box-shadow: 7px 10px 12px -5px rgba(0, 0, 0, 0.56);
  text-align: center;
  background-color: white;
  border-radius: 3px;
}

.Pokecard-data {
  font-size: 80%;
  text-transform: capitalize;
}

.Pokecard-title {
  margin-bottom: 1rem;
  font-weight: bold;
  font-size: 1.2rem;
}

.Pokecard-image {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 150px;
  background-color: #eceff1;
  border-radius: 3px;
  margin-bottom: 1rem;
}

.Pokecard img:hover {
  transform: scale(1.2);
  opacity: 0.8;
  transition: all ease 500ms;
}

.TypeList {
    width: 200px;
    margin-top: 103px;
    box-shadow: 7px 10px 12px -5px rgba(0, 0, 0, 0.56);
    background-color: white;
    border-radius: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

.TypeList-data {
    font-size: 80%;
    box-shadow: 7px 10px 12px -5px rgba(123, 226, 140, 0.56);
    padding: 5px;
}

.TypeList-title {
    margin-bottom: 1rem;
    font-weight: bold;
    font-size: 1.2rem;
    text-align: center;
    box-shadow: 7px 10px 12px -5px rgba(246, 250, 0, 0.56);

}

.TypeList-data:hover {
    transform: scale(1.2);
    color: red;
    transition: all ease 500ms;
}
ul {
    margin-left: -15%;
}

.count {
    font-weight: bold;
    margin-right: 10px;
}

.type {
    text-transform: capitalize;
}
.Pokedex {
  text-align: center;
  margin: 0;
}

.Pokedex-cards {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.Pokedex-winner {
  color: #4caf50;
}

.Pokedex-loser {
  color: #e91e63;
}

.wrapper {
display: flex;
justify-content: space-evenly;
flex-wrap: wrap;
}




body {
    background: #fffc00;
    /* fallback for old browsers */
    /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #ffffff, #fffc00);
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */


}

/* .centerItems h1, h3{
    justify-content: center;

} */

header {
    text-align: center;
}

header h1 {
    margin-top: 50px;
    font-size: 84px;
    font-weight: bold;
    color: rgb(15, 37, 109);
    text-shadow: -1px -1px 0 rgb(255, 0, 0), 1px -1px 0 rgb(255, 196, 0), -1px 1px 0 rgb(0, 0, 0), 1px 1px 0 #000;
}

header h1:hover {
    transform: scale(1.1);
    opacity: 0.9;
    transition: all ease 500ms;
}

header h3 {
    font-size: 2em;
    font-weight: 600;
    letter-spacing: .5px;
    font-family: 'Amatic SC', cursive;

}

.battleCard {
    background: #000428;
    /* fallback for old browsers */
    /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #004e92, #000428);
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

    margin: 80px auto;
    height: 450px;
    max-width: 600px;
    letter-spacing: 1px;
    box-shadow: 10px -20px 30px 20px rgba(7, 7, 7, 0.8);
    /* box-shadow: -17px 10px 12px -5px rgba(0, 0, 0, 0.56); */
    justify-content: center;
    align-items: center;
    text-align: center;
    border-radius: 15px;
    border: 5px ridge #004e92;
}

.battleCard h5 {
    margin-top: 45px;
    margin-bottom: 25px;
    padding-top: 10px;
    padding-bottom: 20px;
    font-size: 26px;
    font-weight: bold;
    font-family: 'Piedra', cursive;
    letter-spacing: 2px;

    color: rgb(255, 166, 0);
    border-bottom: 5px ridge rgb(255, 166, 0);
    border-radius: 4px;
    box-shadow: -17px 10px 12px -5px rgba(0, 0, 0, 0.56);
    text-shadow: -1px -1px 0 rgb(0, 0, 0), 1px -1px 0 rgb(0, 0, 0), -1px 1px 0 rgb(0, 0, 0), 1px 1px 0 #000;

    /* text-shadow: -1px -1px 0 rgb(231, 247, 7), 1px -1px 0 rgb(243, 4, 4), -1px 1px 0 rgb(0, 0, 0), 1px 1px 0 #000; */

}

Button {
    box-shadow: -17px 10px 12px -7px rgba(63, 27, 27, 0.56);
    border-radius: 10px !important;
    letter-spacing: .8px !important;
    /* font-weight: bold !important; */
    font-family: 'Special Elite',
        cursive !important;
    /* padding: 15px !important; */
}

Button:hover {
    transform: scale(1.1);
    color: white !important;
    opacity: 0.9;
    transition: all ease 500ms;
    border: 1px solid white !important;
}

.game-rules {
    color: white !important;
    border: 1px solid white !important;
}
